import React,{Component} from 'react';
import Menuitem from './menuitem';
import { Link } from "react-router-dom";

class Menu extends Component{
    state={ clicked: false}

clickedeventHandler = () => {
    this.setState({clicked: !this.state.clicked});
}

closenavbar = () =>{
    this.setState({clicked: !this.state.clicked}); 
}   
state = {
            scrolling:true,
        }

        componentDidMount(){
            this.setState({scrolling:false});
            window.addEventListener('click',this.click)
            window.addEventListener('scroll', this.componentDidUpdate);
        }
        componentDidUpdate=()=>{
        if(window.scrollY >0 && this.state.scrolling === false){
            this.setState({scrolling:true});
        }else if (window.scrollY === 0 && this.state.scrolling === true) {
        this.setState({scrolling:false});
        }
        var x = window.matchMedia("(min-width: 700px)")
        if (x.matches) {
            if (window.scrollY > 100){
            var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            var hello =  document.getElementsByClassName("hello");
            var scrolllogo =  document.getElementsByClassName("fa-long-arrow-down");
            scrolllogo[0].style.transform = "translateX(48vw)"; 
            halfscreen[0].style.width="95vw";
            twohalfscreen[0].style.width="0vw";
            hello[0].style.left="58vw";
            hello[0].style.width="30vw";
            }
            if (window.scrollY == 0){
             var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            var hello =  document.getElementsByClassName("hello");
            var scrolllogo =  document.getElementsByClassName("fa-long-arrow-down");
            scrolllogo[0].style.transform = "translateX(0vw)"; 
            halfscreen[0].style.width="50vw";
            twohalfscreen[0].style.width="49vw";
            hello[0].style.left="0vw";
            hello[0].style.width="100vw";

            }
        }
        else{
            if (window.scrollY > 50){
            var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            var scrolllogo =  document.getElementsByClassName("fa-long-arrow-down");
            scrolllogo[0].style.transform = "translateX(47vw)"; 
            halfscreen[0].style.width="90vw";
            twohalfscreen[0].style.width="0vw";
            }
            if (window.scrollY < 50){
                var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            var hello =  document.getElementsByClassName("hello");
            var scrolllogo =  document.getElementsByClassName("fa-long-arrow-down");
            scrolllogo[0].style.transform = "translateX(0vw)"; 
            halfscreen[0].style.width="50vw";
            twohalfscreen[0].style.width="49vw";
            }
            
        }
    }
    click=()=>{
        var x = window.matchMedia("(min-width: 700px)")
        if (x.matches) {
        if (this.state.clicked){
            var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            var hello =  document.getElementsByClassName("hello");
            halfscreen[0].style.width="95vw";
            twohalfscreen[0].style.width="0vw";
            hello[0].style.left="58vw";
            hello[0].style.width="30vw";
            }
            if (!this.state.clicked){
            var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            var hello =  document.getElementsByClassName("hello");
            halfscreen[0].style.width="50vw";
            twohalfscreen[0].style.width="49vw";
            hello[0].style.left="0vw";
            hello[0].style.width="100vw";
            }
        }
        else{
            if (this.state.clicked){
            var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            halfscreen[0].style.width="90vw";
            twohalfscreen[0].style.width="0vw";
            }
            if (!this.state.clicked){
                var halfscreen = document.getElementsByClassName("halfscreen"); 
            var twohalfscreen = document.getElementsByClassName("twohalfscreen");
            var hello =  document.getElementsByClassName("hello");
                halfscreen[0].style.width="50vw";
            twohalfscreen[0].style.width="49vw";
            }
        }
    }

    render(){
    return(
        <div className='menu'>
            <input
                type="checkbox"
                checked={this.state.clicked}
                className="checkboxs"
            />
            <div className='menubackscreen'><div className='colororg'></div></div>
            <div className='listhover'></div>
            <ul className='list'>
                {/* <div className='navbar-band title'>SURAKSH.Aboutme</div> */}
                {Menuitem.map((item, index) => {
                return(
                <Link className='links' key={index} to={item.link}>
                <li><a className={item.cname}><span>{item.name}</span></a></li>
                </Link>
                    )
                }
            )}
                <div className='closeicon' onClick={this.clickedeventHandler}><i className='fas fa-times'></i></div>
            </ul> 
           
            <div className='menu-icon' onClick={this.clickedeventHandler}>
                <svg width="auto" style={{position:"absolute"}}>
                <line x1="15" y1="28" x2="15" y2="65" style={{stroke:"rgb(0,0,0)"}} />
                </svg>
                <svg width="auto" style={{position:"absolute"}}>
                <line x1="20" y1="28" x2="20" y2="45" style={{stroke:"rgb(0,0,0)"}} />
                </svg>
                <svg width="auto" style={{position:"absolute"}}>
                <line  x1="25" y1="28" x2="25" y2="50" style={{stroke:"rgb(0,0,0)"}} />
                </svg>
                <svg width="auto" style={{position:"absolute"}}>
                <line  x1="30" y1="28" x2="30" y2="60" style={{stroke:"rgb(0,0,0)"}} />
                </svg>
                <div>M</div>
                <div>E</div>
                <div>N</div>
                <div>U</div>
                </div>
        </div>
    );
    }
}
export default Menu;